* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --bg-color: #f6f6f6;
  --second-bg-color: #e1e1e1;
  --third-bg-color: #b0a4a4;
  --font-size-1: 1.6rem;
  --font-size-2: 1.2rem;
  --font-size-3: 1rem;
  --text-color: #081b29;
  --main-color: #00abf0;
}

.app {
  width: 100%;
  height: 100vh;
  background-color: var(--bg-color);
}

.contact-section {
  /* border: 2px solid red; */
  width: 95%;
  margin-inline: auto;
  height: 6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.icons {
  border-left: 2px solid gray;
}

.icons img {
  width: 38px;
  margin: 10px 10px;
}

.icons img:hover {
  transform: scale(1.1) translateY(-5px);
  transition: 0.3s ease;
  cursor: pointer;
}

.bottom-line {
  position: absolute;
  top: 6rem;
  /* left: 0; */
  width: 95%;
  border: 2px solid black;
  margin-inline: auto;
}

.connect-us {
  border-left: 2px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.connect-us a {
  color: black;
  text-decoration: none;
  margin-inline: 5px;
}

.connect-us img {
  width: 34px;
}

.connect-us h3 {
  font-size: var(--font-size-2);
  padding: 4px;
}

/* Header section */
.header {
  overflow-x: scroll;
  height: 6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header::-webkit-scrollbar {
  display: none;
}
.logo {
  display: flex;
  margin: 0 10px;
  cursor: pointer;
}

.logo img {
  width: 62px;
  object-fit: cover;
  object-position: 10px;
}

.logo-content h2 {
  font-size: var(--font-size-1);
  margin-top: 3px;
  color: #777;
}

.logo-content p {
  color: rgba(240, 10, 49, 0.973);
}

.header-props {
  list-style: none;
  display: flex;
  color: var(--text-color);
  font-weight: bold;
  cursor: pointer;
}

.header-props li {
  font-size: var(--font-size-2);
  padding: 5px;
  margin: 0 5px;
  border-radius: 20px 20px 0 0;
  transition: 0.5s ease;
}

.active {
  background-color: var(--second-bg-color);
  transform: translateY(2rem);
  text-decoration: underline;
}
.user-field {
  width: 300px;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 2rem;
}
.search input {
  width: 200px;
  height: 2rem;
  font-size: var(--font-size-3);
  transition: 0.5s ease;
}
.search-display {
  display: none;

  width: 0px;
}

.user-field img {
  width: 32px;
  margin: -12px 5px;
}

/* Main body  */
.main-section,
.header-view {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  height: 95%;
  width: 100%;
  text-align: center;
  background-color: var(--second-bg-color);
}

.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 10px 0;
  background-color: var(--third-bg-color);
  border-radius: 20rem;
}

.main-content img {
  width: 28%;
  display: flex;
  justify-content: space-between;
}

.main1 {
  width: 90%;
  /* border: 2px solid red; */
}

.content {
  width: 40%;
}

.btn {
  display: block;
  width: 200px;
  height: 2.5rem;
  font-size: var(--font-size-3);
  font-weight: bold;
  margin-inline: auto;
  margin-top: 3rem;
  cursor: pointer;
}

.main3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  align-items: center;
  width: 90%;
  height: 8rem;
  margin-inline: auto;
  background-color: var(--third-bg-color);
}

.ul-1,
.ul-2 {
  width: 95%;
  list-style: none;
  font-size: var(--font-size-2);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ul-1 {
  border-bottom: 2px solid var(--bg-color);
}

.purple {
  background-color: rgba(173, 74, 173, 0.849);
}

.view {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.view .left-main {
  background-color: #9db2bf;
  width: 30%;
  height: 90%;
  overflow: scroll;
}

.view .left-main::-webkit-scrollbar {
  display: none;
}

.view .left-main li {
  position: relative;
  list-style: none;
  font-size: var(--font-size-2);
  color: var(--text-color);
  padding: 5px 0;
  margin: 5px 0;
  font-weight: bold;
  border-bottom: 2px solid black;
  cursor: pointer;
}

.view .right-main {
  display: flex;
  justify-content: center;
  /* align-items: center; */
  background-color: #9db2bf;
  width: 60%;
  height: 90%;
}

.arrow {
  font-size: 2rem;
  position: absolute;
  right: 0;
  top: 0;
}

.engineer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  overflow: scroll;
}

.engineer::-webkit-scrollbar {
  display: none;
}

@media (max-width: 1198px) {
  :root {
    --font-size-1: 1.4rem;
    --font-size-2: 1.1rem;
    --font-size-3: 1rem;
  }

  .main-section {
    height: auto;
    padding: 1rem 0;
  }

  .user-field {
    width: 242px;
  }
  .search input {
    width: 150px;
  }
}
@media (max-width: 1096px) {
  :root {
    /* --bg-color: red; */
    --font-size-2: 1.05rem;
  }

  .header {
    position: relative;
  }

  .search input {
    width: 250px;
  }

  .user-field {
    width: 400px;
    margin: 10px 0;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
  }
  .header {
    gap: 2rem;
    margin-bottom: 1rem;
    justify-content: flex-start;
    align-items: end;
  }
  .active {
    background-color: var(--bg-color);
    transform: translateY(-0.5rem);
    text-decoration: underline;
  }
  .main-section,
  .header-view {
    height: 30rem;
    overflow: scroll;
  }
}

@media (max-width: 922px) {
  :root {
    /* --bg-color: red; */
    --font-size-1: 1.1rem;
    --font-size-2: 1rem;
  }

  h1 {
    font-size: 1.4rem;
  }
  h2 {
    font-size: 1.1rem;
  }

  .connect-us {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

@media (max-width: 836px) {
  .header {
    margin: 12px 0;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .header-props {
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-inline: auto;
    transform: translateX(-50%);
  }

  .header-props::-webkit-scrollbar {
    display: none;
  }

  h3 {
    font-size: 1.05rem;
    margin-left: 10px;
  }

  .logo {
    height: 60px;
    border-right: 2px solid #00abf0;
    border-bottom: 2px solid #00abf0;
    padding-bottom: 0;
    padding-right: 10px;
    border-radius: 10px;
  }
}

@media (max-width: 698px) {
  .main-content img {
    display: none;
  }
  .main-content {
    border-radius: 50px;
  }

  .btn {
    border-radius: 10px;
    background-color: #00abf0;
    box-shadow: 1px 2px #081b29;
  }

  .header-props {
    max-width: 100%;
    overflow: scroll;
  }

  h3 {
    font-size: 0.8rem;
  }

  .icons img {
    margin: 0;
  }
}

@media (max-width: 580px) {
  :root {
    --font-size-1: 1rem;
    --font-size-2: 0.8rem;
    --font-size-3: 0.6rem;
  }

  h1 {
    font-size: 1rem;
    margin-top: 1rem;
  }

  .connect-us a img,
  .icons img,
  .user-field img {
    width: 28px;
  }

  .logo img {
    display: none;
  }
  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
  }
}

@media (max-width: 420px) {
  .main-section {
    height: auto;
  }

  .content {
    width: 90%;
  }

  .main3 {
    height: auto;
    border-radius: 50px;
  }

  .btn {
    width: 70%;
  }

  .ul-1,
  .ul-2 {
    display: block;
    margin-bottom: 20px;
  }
  .ul-1 li,
  .ul-2 li {
    padding: 10px;
  }

  .view {
    flex-direction: column;
    width: 100%;
    gap: 0;
  }

  .view .right-main {
    width: 100%;
    height: auto;
  }
  .view .left-main {
    width: 100%;
    height: auto;
    display: flex;
    overflow-x: scroll;
    flex-direction: row;
    border-bottom: 2px solid #1c1f20;
  }
  .view .left-main li {
    height: 20px;
    padding: 0px 20px;
    border: none;
    border-right: 2px solid black;
  }

  .left-main li {
    width: 200px;
  }

  .icons h3 {
    display: none;
  }
}
